import Swiper from "swiper";

document.addEventListener("DOMContentLoaded", function () {
	const projects = document.querySelectorAll(".projects__grid-project");

	if (window.innerWidth <= 768) {
		projects.forEach((project) => {
			console.log(project);
			project.classList.add("projects__grid-project--mobile");
		});

		const tabsContent = document.querySelectorAll(".projects .block-tabs__tab-content");

		tabsContent.forEach((tabContent, i) => {
			tabContent.classList.add(`swiper-${i}`);
			tabContent.querySelector(".projects__grid").classList.add("swiper-wrapper");
			tabContent.querySelectorAll(".projects__grid-project").forEach((project) => {
				project.classList.add("swiper-slide");
			});

			const swiper = new Swiper(`.projects .swiper-${i}`, {});
			tabContent.querySelectorAll(".projects__btn").forEach((btn) => {
				btn.addEventListener("click", () => {
					if (btn.classList.contains("projects__btn--next")) {
						swiper.slideNext();
					}
					if (btn.classList.contains("projects__btn--prev")) {
						swiper.slidePrev();
					}
				});
			});
		});
	}
});
