import Swiper from "swiper";

document.addEventListener("DOMContentLoaded", function () {
	const tabsBlocks = document.querySelectorAll(".block-tabs");
	tabsBlocks.forEach((tabsBlock) => {
		const tabs = tabsBlock.querySelectorAll(".block-tabs__tab");
		const tabContents = tabsBlock.querySelectorAll(".block-tabs__tab-content");

		tabs.forEach((tab) => {
			tab.classList.remove("block-tabs__tab--active");
		});
		tabContents.forEach((tabContent) => {
			tabContent.classList.remove("block-tabs__tab-content--active");
		});
		tabs[0].classList.add("block-tabs__tab--active");
		tabContents[0].classList.add("block-tabs__tab-content--active");
		tabs.forEach((tab, i) => {
			tab.addEventListener("click", function (event) {
				event.preventDefault();
				tabs.forEach((tab) => {
					tab.classList.remove("block-tabs__tab--active");
				});
				tab.classList.add("block-tabs__tab--active");
				tabContents.forEach((tabContent) => {
					tabContent.classList.remove("block-tabs__tab-content--active");
				});
				tabContents[i].classList.add("block-tabs__tab-content--active");
			});
		});
		if (window.innerWidth <= 1024) {
			tabContents.forEach((tabContent, i) => {
				tabContent.classList.add(`swiper-${i}`);
				const swiperBlock = document.createElement("div");
				const cols = tabContent.querySelectorAll(".block-tabs__col");

				swiperBlock.className = "swiper-wrapper";
				tabContent.insertBefore(swiperBlock, tabContent.querySelector(".block-tabs__btns"));
				cols.forEach((col) => {
					col.classList.add("swiper-slide");
					swiperBlock.append(col);
				});

				const swiper = new Swiper(`.practices .swiper-${i}`, {
					slidesPerView: 1,
					breakpoints: {
						600: {
							slidesPerView: 2,
						},
					},
					freeMode: true,
				});
				tabContent.querySelectorAll(".block-tabs__btn").forEach((btn) => {
					btn.addEventListener("click", () => {
						if (btn.classList.contains("block-tabs__btn--next")) {
							swiper.slideNext();
						}
						if (btn.classList.contains("block-tabs__btn--prev")) {
							swiper.slidePrev();
						}
					});
				});
			});
		}
	});
});
